          cat: "1",
          available: true,
        },
        {
          title: "カテゴリ「砲兵隊」",
          cat: "1",
          available: false,
        },
        {
          title: "カテゴリ「ガンシップ」",
          cat: "1",
          available: false,
        },
        {
          title: "カテゴリ「爆撃機」",
          cat: "1",
          available: false,
        },
        {
          title: "カテゴリ「ミサイル」",
          cat: "1",
          available: false,
        },
        {
          title: "カテゴリ「衛星」",
          cat: "1",
          available: false,
        },
        {
          title: "カテゴリ「リムペットガン」",
          cat: "1",
          available: false,
        },
        {
          title: "カテゴリ「特殊」",
          cat: "1",
          available: false,
        },
        {
          title: "強襲エアクラフト",
          cat: "1",
          available: true,
        },
        {
          title: "追走カプセル",
          cat: "1",
          available: true,
        },
        {
          title: "デスバード",
          cat: "1",
          available: true,
        },
        {
          title: "ブラッカー",
          cat: "2",
          available: true,
        },
        {
          title: "グレイプ",
          cat: "2",
