        },
        {
          title: "トーチカ禁止",
          cat: "3",
          available: true,
        },
        {
          title: "バックパック禁止",
          cat: "3",
          available: true,
        },
        {
          title: "エレクトロン禁止",
          cat: "3",
          available: true,
        },
        {
          title: "爆撃機禁止",
          cat: "3",
          available: false,
        },
        {
          title: "機銃掃射禁止",
          cat: "3",
          available: false,
        },
        {
          title: "爆発物のみ",
          cat: "3",
          available: false,
        },
        {
          title: "救助禁止",
          cat: "3",
          available: false,
        },
      ],
      challenge_list_cat: [],
      challenge_active_enough: true,
    };
  },
  mounted() {
    this.targetselect();
    this.start();
    this.create_challenge_list();
  },
  components: {},
  methods: {
    targetselect() {
      let list = [];
      if (this.select_main) {
        list = list.concat(this.$missions.Main);
        if (this.select_online) {
          list = list.concat(this.$missions.Online);
        }
      }
      if (this.select_dlc1) {
        list = list.concat(this.$missions.DLC1);
      }
      if (this.select_dlc2) {
        list = list.concat(this.$missions.DLC2);
      }
      if (!this.select_main && !this.select_dlc1 && !this.select_dlc2) {
        list = null;
        this.log.unshift("ランダム化するミッションパックを選択してください");
      }
      this.target = list;
      console.log("ターゲット変更");
    },
    start() {
      let elm = document.getElementsByClassName("transition");
      for (var i = 0; i < elm.length; i++) {
        elm[i].classList.add("nowloading");
      }
      setTimeout(this.shuffle, 800);
    },
    challenge_reroll() {
      let arr = [];
      let numArr = [];
      let challengenum = 0;
      let resval = 1;

      for (var i = 0; i < this.challenge_list.length; i++) {
        if (this.challenge_list[i].available) {
          arr.unshift(this.challenge_list[i]);
          challengenum = challengenum + 1;
        }
      }
      if (this.result_challenge_val == "rnd") {
        resval = Math.floor(Math.random() * 3) + 1;
      } else {
        resval = this.result_challenge_val;
      }

      for (var j = 0, len = arr.length; j < resval; j++, len--) {
        let rndNum = Math.floor(Math.random() * len);
        numArr.push(arr[rndNum]);
        arr[rndNum] = arr[len - 1];
      }

      this.result_challenge = numArr;
      console.log(challengenum);
      if (challengenum >= resval) {
        this.challenge_active_enough = true;
      } else {
        this.challenge_active_enough = false;
      }
    },
    create_challenge_list() {
      let listarr = { weapon: [], vehicle: [], objective: [] };
      for (var i = 0; i < this.challenge_list.length; i++) {
        if (this.challenge_list[i].cat == 1) {
          listarr.weapon.push(this.challenge_list[i]);
        } else if (this.challenge_list[i].cat == 2) {
          listarr.vehicle.push(this.challenge_list[i]);
        } else if (this.challenge_list[i].cat == 3) {
          listarr.objective.push(this.challenge_list[i]);
        }
        this.challenge_list_cat = listarr;
      }
      console.log(this.challenge_list_cat);
    },
    shuffle() {
      if (this.target == null) {
        this.result = {
          onID: "--",
          ID: "ミッションパックを選択してください",
          name: "",
          status: "0",
        };
      } else {
        const rnd = Math.floor(Math.random() * this.target.length);
        this.result = this.target[rnd];
        this.challenge_reroll();
        this.log.unshift(this.target[rnd].name + "が選択されました");
      }
      let elm = document.getElementsByClassName("transition");
      for (var i = 0; i < elm.length; i++) {
        elm[i].classList.remove("nowloading");
      }
    },
  },
};
</script>

