    }
    .label {
      display: inline-flex;
      align-items: center;
      width: 100%;
      box-sizing: border-box;
      position: relative;
      height: 1.5rem;
      font-weight: bold;
    }
    .label p {
      position: relative;
      display: block;
      z-index: 1;
      padding-left: 2rem;
      box-sizing: border-box;
      font-size: clamp(0.8rem, 1.5vw, 1.2rem);
      span {
        display: inline-block;
        margin-right: 0.25em;
      }
    }
    .label::before {
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      content: "";
      width: 1.5rem;
      height: 1.5rem;
      border: 2px solid white;
      transition: 0.25s $easing;
      z-index: 0;
    }
    .label::after {
      position: absolute;
      top: 4px;
      left: 4px;
      display: block;
      content: "";
      width: calc(1.5rem - 8px);
      height: calc(1.5rem - 8px);
      background-color: rgb(37, 191, 59);
      z-index: 0;
      opacity: 0;
    }
    li:has(input[type="checkbox"]:checked) {
      background: linear-gradient(90deg, rgb(7, 39, 0), rgb(0, 0, 0));
      filter: drop-shadow(0px 0px 4px rgba(37, 191, 59, 0.8));
    }
    input[type="checkbox"]:checked + .label::after {
      opacity: 1;
    }
  }
